.stars {
  width: 100%;
  height: 16px;
}

.stars:before {
  content: "";
  background-image: url("star.75a8b2fa.svg");
  background-repeat: repeat-x;
  background-size: 16px 16px;
  width: 100px;
  height: 16px;
  display: block;
}

.stars--2:before {
  background-image: url("star-active.896e078e.svg");
  background-position: 0 0, 42px 0;
  background-repeat: repeat-x;
  background-size: 16px 16px;
}

.stars--4:before {
  background-image: url("star-active.896e078e.svg");
  background-position: 0 0, 84px 0;
  background-repeat: repeat-x;
  background-size: 16px 16px;
}

.card {
  box-sizing: border-box;
  background-color: #fff;
  border-radius: 8px;
  flex-direction: column;
  gap: 20px;
  width: 200px;
  padding: 18px;
  display: flex;
  box-shadow: 0 4px 12px #00000026;
}

.card__image {
  background-image: url("imac.202c52ff.jpeg");
  background-position: center;
  background-repeat: no-repeat;
  background-size: contain;
  align-self: center;
  width: 160px;
  height: 134px;
}

.card__content {
  flex-direction: column;
  gap: 5px;
  display: flex;
}

.card__title {
  color: #000;
  margin: 0;
  font-family: Roboto, sans-serif;
  font-size: 13px;
  font-weight: 500;
}

.card__code {
  color: #616070;
  margin: 0;
  font-family: Roboto, sans-serif;
  font-size: 13px;
}

.card__rating {
  justify-content: space-between;
  align-items: center;
  display: flex;
}

.card__reviews {
  color: #000;
  margin: 0;
  font-family: Roboto, sans-serif;
  font-size: 13px;
}

.card__price {
  justify-content: space-between;
  align-items: center;
  display: flex;
}

.card__price-title {
  color: #616070;
  font-family: Roboto, sans-serif;
  font-size: 13px;
}

.card__price-value {
  color: #000;
  font-family: Roboto, sans-serif;
  font-size: 13px;
  font-weight: 700;
}

.card__button {
  color: #fff;
  background-color: #00acdc;
  border: 1px solid #00acdc;
  border-radius: 5px;
  justify-content: center;
  align-items: center;
  height: 40px;
  font-family: Roboto, sans-serif;
  font-weight: 700;
  text-decoration: none;
  display: flex;
}

.card__button:hover {
  color: #00acdc;
  background-color: #fff;
}

body {
  background-color: #f3f3f3;
  justify-content: center;
  margin: 0;
  padding-top: 40px;
  font-family: Roboto, sans-serif;
  display: flex;
}
